@import url(https://css.gg/spinner-alt.css);
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  width: 100vw;
}
.header{
    background-image: url(/pizzavilag/static/media/header.f34d0ef1.jpg);
    background-position: center center;
    background-size: cover;
    width: 50%;
    border-radius: 15px 50px;
    text-shadow: 0px 2px 4px black;
    position: relative;
    max-width: 800px;
}

.i{
    position: absolute;
    right: 10%;
    cursor: context-menu;
    font-size: 3vw;
}

.cms{
    cursor: context-menu;
}
nav{
    height: 100px;
}
.cardBox{
    height: 380px;
    
}

.pizzaName{
    height: 30px;
}

.topping{
    height: 70px;
}


.delete
{
    font-size: 0;
}

.shoppingCartList
{
    position: relative;
}

.shoppingCartList:hover
{
    background-color: rgba(255, 0, 0, .6)
}

.shoppingCartList:hover > .delete
{
    height: 50%;
    font-size: 20px;
    color: white;
    z-index: 1;
    position: absolute;
    top: 50%;
}

.gg-shopping-cart {
    display: block;
    box-sizing: border-box;
    position: relative;
    transform: scale(1);
    transform: scale(var(--ggs,1));
    width: 20px;
    height: 21px;
    background:
    linear-gradient(
    to left,currentColor 12px,
    transparent 0)
    no-repeat -1px 6px/18px 2px,
    linear-gradient(
    to left,currentColor 12px,
    transparent 0)
    no-repeat 6px 14px/11px 2px,
    linear-gradient(
    to left,currentColor 12px,
    transparent 0)
    no-repeat 0 2px/4px 2px,
    radial-gradient(
    circle,currentColor 60%,
    transparent 40%)
    no-repeat 12px 17px/4px 4px,
    radial-gradient(
    circle,currentColor 60%,
    transparent 40%)
    no-repeat 6px 17px/4px 4px
   }
   
   .gg-shopping-cart::after,
   .gg-shopping-cart::before {
    content: "";
    display: block;
    position: absolute;
    box-sizing: border-box;
    width: 2px;
    height: 14px;
    background: currentColor;
    top: 2px;
    left: 4px;
    transform: skew(12deg)
   }
   
   .gg-shopping-cart::after {
    height: 10px;
    top: 6px;
    left: 16px;
    transform: skew(-12deg)
   } 

   .gg-trash {
    box-sizing: border-box;
    position: relative;
    display: block;
    transform: scale(1);
    transform: scale(var(--ggs,1));
    width: 10px;
    height: 12px;
    border: 2px solid transparent;
    box-shadow:
    0 0 0 2px,
    inset -2px 0 0,
    inset 2px 0 0;
    border-bottom-left-radius: 1px;
    border-bottom-right-radius: 1px;
    margin-top: 4px
   }
   
   .gg-trash::after,
   .gg-trash::before {
    content: "";
    display: block;
    box-sizing: border-box;
    position: absolute
   }
   
   .gg-trash::after {
    background: currentColor;
    border-radius: 3px;
    width: 16px;
    height: 2px;
    top: -4px;
    left: -5px
   }
   
   .gg-trash::before {
    width: 10px;
    height: 4px;
    border: 2px solid;
    border-bottom: transparent;
    border-top-left-radius: 2px;
    border-top-right-radius: 2px;
    top: -7px;
    left: -2px
   } 




.App {
  text-align: center;
}
@-webkit-keyframes spinneralt {
  0% { transform: rotate(0deg) }
  to { transform: rotate(359deg) }
 }
@keyframes spinneralt {
  0% { transform: rotate(0deg) }
  to { transform: rotate(359deg) }
 }
 .gg-spinner-alt {
  transform: scale(1);
  transform: scale(var(--ggs,1))
 }
 .gg-spinner-alt,
 .gg-spinner-alt::before {
  box-sizing: border-box;
  position: relative;
  display: block;
  width: 120px;
  height: 120px
 }
 .gg-spinner-alt::before {
  content: "";
  position: absolute;
  border-radius: 100px;
  -webkit-animation: spinneralt 1s cubic-bezier(.6,0,.4,1) infinite;
          animation: spinneralt 1s cubic-bezier(.6,0,.4,1) infinite;
  border: 3px solid transparent;
  border-top-color: currentColor
 } 
.appBody
 {
    background-color: #FAD784;
    height: auto;
    min-height: 100vh;
    width: auto;
    min-width: 100vw;
    font-style: normal;
    font-weight: normal;
 }

